lor:var(--yelow);font-size:1.3rem;border:1px solid grey;color:white;outline:none}textarea.con-area:focus{border:1px solid var(--yelow);padding-left:1rem;padding-right:-1rem;transition:all 0.4s;-webkit-transition:all 0.4s;-moz-transition:all 0.4s;-ms-transition:all 0.4s;-o-transition:all 0.4s}button.con-sub.btn{justify-self:center;margin-top:2rem;padding:1rem;font-size:1.3rem;border-radius:3px;font-weight:100;background:white;border:1px solid var(--yelow);cursor:pointer}button.con-sub.btn:hover{border:1px solid;background-color:#68187a;transition:all 0.4s;border-radius:1rem}@media only screen and (max-width: 720px){div.info{grid-template-columns:repeat(auto-fit, minmax(130px, 1fr))}}

